* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Heebo, Arial, sans-serif;
}

.container {
  margin: 0 auto;
  max-width: 1100px;
}

.content-container {
  margin: 0 auto;
  max-width: 1000px;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

@font-face {
  src: url("../fonts/Heebo-Bold.woff2") format("woff2"), url("../fonts/Heebo-Bold.woff") format("woff");
