/* global blocks */
.basic-link {
  align-self: center;
  justify-self: flex-end;
  grid-area: link;
  font-family: var(--basic-font);
  font-weight: var(--s-text-weight);
  font-size: var(--s-text-size);
  text-decoration: none;
  color: var(--link-text);
}
.basic-link:hover {
  text-decoration: underline;
}

.body {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  background-color: var(--bg);
}
.body--scrolloff {
  overflow: hidden;
}

.footer {
  padding: 50px var(--safe-side-paddings);
  width: 100%;
}

.footer__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 25px;
}

.footer__social-list {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  gap: 35px;
}

.footer__social-item {
  width: 30px;
  height: 30px;
}

.footer__social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}

.footer__copyright {
  font-family: var(--basic-font);
  font-weight: var(--xs-text-weight);
  font-size: var(--xs-text-size);
  color: var(--primary-text);
}

.footer__social-icon path {
  fill: var(--primary-text);
}
.footer__social-icon:hover path {
  fill: var(--hover-item-color);
}

.header {
  padding: 30px var(--safe-side-paddings);
  width: 100%;
}

.header__wrap {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 40px;
}

.main {
  width: 100%;
}

.theme-switcher {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 0;
  border: none;
  padding: 5px;
  width: 106px; /* padding 5px + (32px icon size * 3 icons) */
}

/* Switcher Radio */
.theme-switcher__radio {
  appearance: none;
  margin: 0;
  width: 32px;
  height: 32px;
  background-position: center;
  background-size: 24px;
  background-repeat: no-repeat;
  transition: filter 0.1s ease-in;
  cursor: pointer;
}
.theme-switcher__radio:focus {
  outline: none;
}
.theme-switcher__radio--light {
  background-image: url("../images/icons/light.svg");
}
.theme-switcher__radio--auto {
  background-image: url("../images/icons/auto.svg");
}
.theme-switcher__radio--dark {
  background-image: url("../images/icons/dark.svg");
}

/* Switcher Status */
.theme-switcher__status {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
  background-image: url("../images/icons/status.svg");
  background-position: center;
  background-size: 30px;
  background-repeat: no-repeat;
  transition: background-position 0.1s ease-in;
}

.theme-switcher__radio:focus-visible ~ .theme-switcher__status {
  box-shadow: 0 0 0 2px black;
}

.theme-switcher__radio--light:checked ~ .theme-switcher__status {
  background-position: left 6px center;
}

.theme-switcher__radio--auto:checked ~ .theme-switcher__status {
  background-position: center center;
}

.theme-switcher__radio--dark:checked ~ .theme-switcher__status {
  background-position: right 6px center;
}

/* blocks */
.nav-list {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  gap: 30px;
}

.nav-list__item {
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-list__link {
  font-family: var(--basic-font);
  font-weight: var(--l-fat-text-weight);
  font-size: var(--l-fat-text-size);
  text-decoration: none;
  text-transform: capitalize;
  color: var(--navigation-link-color);
  cursor: pointer;
}
.nav-list__link:hover {
  color: var(--hover-item-color);
}
.nav-list__link--active {
  color: var(--current-item-color);
  pointer-events: none;
}

.nav {
  display: flex;
}

